import VMapbox from './components/VMapbox';
import VMapboxCluster from './components/VMapboxCluster';
import VMapboxGeocoder from './components/VMapboxGeocoder';
import VMapboxLayer from './components/VMapboxLayer';
import VMapboxMarker from './components/VMapboxMarker';
import VMapboxNavigationControl from './components/VMapboxNavigationControl';
import VMapboxPopup from './components/VMapboxPopup';
import VMapboxSource from './components/VMapboxSource';

/**
 * Install all components
 *
 * @param  {Vue}  Vue The Vue object
 * @return {void}
 */
function install(Vue) {
  Vue.component('v-mapbox', VMapbox);
  Vue.component('v-mapbox-cluster', VMapboxCluster);
  Vue.component('v-mapbox-geocoder', VMapboxGeocoder);
  Vue.component('v-mapbox-layer', VMapboxLayer);
  Vue.component('v-mapbox-marker', VMapboxMarker);
  Vue.component('v-mapbox-navigation-control', VMapboxNavigationControl);
  Vue.component('v-mapbox-popup', VMapboxPopup);
  Vue.component('v-mapbox-source', VMapboxSource);
}

// Export each components separately
export {
  VMapbox,
  VMapboxCluster,
  VMapboxGeocoder,
  VMapboxLayer,
  VMapboxMarker,
  VMapboxNavigationControl,
  VMapboxPopup,
  VMapboxSource,
};

// Export the install function as default
export default install;
